@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: 'Space Mono', monospace;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, p {
  margin: 0;
}

a {
  text-decoration: none;
  transition: all .2s ease-in-out;
}

a:hover {
  opacity: .8;
}
:root {
  --color-black: #222731;
  --color-white: #FEFEFE;
  --color-error: #F74646;
}

.App.Lightmode {
  --main-text-color: var(--color-black);
  --body-background: #F6F8FF;
  --bg-component: var(--color-white);
  --bg-component-2: #F6F8FF;
}

.App.Darkmode {
  --main-text-color: #FFFFFF;
  --body-background: #141D2F;
  --bg-component: #1E2A47;
  --bg-component-2: #141D2F;
}

.App {
  height: 100vh;
  width: 100vw;
  margin: 0;
  background-color: var(--body-background);
  transition: all .2s ease-in-out;
}

.Lightmode .Search,
.Lightmode .Information {
  box-shadow: 0px 0px 5px 0px rgb(0 0 0 / 28%);
}

.Disabled {
  opacity: .5;
}

.Container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  width: 650px;
  height: -webkit-min-content;
  height: min-content;
  padding: 15px 20px;
}

.Header {
  display: flex;
  margin-bottom: 10px;
  color: var(--main-text-color);
}

.Header__Container {
  display: flex;
  align-items: center;
}

.Header .Header__Container:last-child {
  margin-left: auto;
  cursor: pointer;
  transition: all .2s ease-in-out;
} 

.Header .Header__Container:last-child i {
  margin-left: 15px;
  font-size: 24px;
  color: var(--main-text-color);
}

.Header__Container-Text {
  text-transform: uppercase;
  font-weight: 800;
  color: var(--main-text-color);
}

.Search {
  padding: 10px 25px;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  background-color: var(--bg-component);
  border-radius: 15px;
}

.Search i {
  font-size: 24px;
  color: blue;
}

.Search__Input {
  background-color: transparent;
  border: none;
  flex-grow: 2;
  padding: 0px 15px;
  color: var(--main-text-color);
}

.Search__Input::-webkit-input-placeholder {
  color: var(--main-text-color);
  opacity: .8;
}

.Search__Input:-ms-input-placeholder {
  color: var(--main-text-color);
  opacity: .8;
}

.Search__Input::placeholder {
  color: var(--main-text-color);
  opacity: .8;
}

.Search__Input:focus {
  outline: none;
}

.Search__Button {
  background: #0079FF;
  padding: 15px 25px;
  border-radius: 15px;
  border: none;
  color: #FEFEFE;
  color: var(--color-white);
  font-weight: 800;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.Information {
  padding: 25px 25px;
  background-color: var(--bg-component);
  border-radius: 15px;
}

.Information__Header {
  display: flex;
}

.Information__Header-ImageContainer {
  width: 15%;
  border-radius: 100%;
  margin-right: 25px;
}

.Information__Header-ImageContainer img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

.Information__Header-InformationContainer {
  display: flex;
  width: 85%;
}

.Information__Header-InformationContainer-Username {
  color: #0079FF;
}

.Information__Header-InformationContainer-Profilename {
  color: var(--main-text-color);
}

.Information__Header-InformationContainer-Date {
  color: var(--main-text-color);
}

.Information__Header-InformationContainer-Right {
  margin-left: auto;
}

.Information__Header-InformationContainer-Username {
  margin-bottom: 15px;
}

.Information__BiographyContainer {
  width: calc(85% - 25px);
  margin-left: auto;
  margin-bottom: 25px;
}

.Information__BiographyContainer-Text {
  color: var(--main-text-color);
}

.Information__StatsContainer {
  width: calc(85% - 55px);
  background-color: var(--bg-component-2);
  display: flex;
  padding: 15px 20px;
  border-radius: 15px;
  margin-left: auto;
  margin-bottom: 25px;
  color: var(--main-text-color);
}

.Information__StatsContainer-Container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.Information__StatsContainer-Container-Title {
  font-weight: 800;
  margin-bottom: 8px;
}

.Information__ExtraContainer {
  width: calc(85% - 25px);
  display: flex;
  margin-left: auto;
}

.Information__ExtraContainer-Container {
  flex-grow: 1;
}

.Information__ExtraContainer-Container-Element {
  display: flex;
  align-items: center;
}

.Information__ExtraContainer-Container .Information__ExtraContainer-Container-Element:first-child {
  margin-bottom: 10px;
}

.Information__ExtraContainer-Container-Element i {
  font-size: 20px;
  margin-right: 15px;
  color: var(--main-text-color);
}

.Information__ExtraContainer-Container-Element p {
  color: var(--main-text-color);
  word-break: break-all;
}

@media only screen and (min-width: 1024px) {
  .Header .Header__Container:last-child:hover {
    opacity: .5;
  }

  .Search__Button:hover {
    background: #006adf;
  }
}

@media only screen and (max-width: 1024px) and (min-width: 768px) {
  .Container {
    width: 60%;
  }

  .Information__Header {
    margin-bottom: 15px;
  }

  .Information__Header-InformationContainer {
    flex-direction: column;
  }

  .Information__Header-InformationContainer-Left {
    height: -webkit-min-content;
    height: min-content;
  }

  .Information__Header-InformationContainer-Right {
    margin-left: 0;
  }

  .Information__Header-InformationContainer-Username {
    margin-bottom: 5px;
  }

  .Information__BiographyContainer {
    width: 100%;
    margin-left: 0;
    margin-bottom: 25px;
  }

  .Information__StatsContainer {
    width: auto;
    padding: 15px 20px;
    margin-left: 0;
  }

  .Information__ExtraContainer {
    width: 100%;
    margin-left: 0;
  }
}

