  .Container {
    padding-top: 20px;
    position: relative;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    width: 90%;
    top: 0;
  }

  .Search {
    padding: 10px 5px;
  }

  .Search__Input {
    padding: 0px 5px;
  }

  .Search__Button {
    padding: 10px 10px;
  }

  .Information__Header {
    margin-bottom: 15px;
  }

  .Information__Header-InformationContainer {
    flex-direction: column;
  }

  .Information__Header-InformationContainer-Left {
    height: -webkit-min-content;
    height: min-content;
  }

  .Information__Header-InformationContainer-Right {
    margin-left: 0;
  }

  .Information__Header-InformationContainer-Username {
    margin-bottom: 5px;
  }

  .Information__Header-ImageContainer {
    width: 40%;
  }

  .Information__StatsContainer-Container {
    font-size: 12px;
  }

  .Information__BiographyContainer {
    width: 100%;
    margin-left: 0;
    margin-bottom: 25px;
  }

  .Information__StatsContainer {
    width: auto;
    padding: 15px 20px;
    margin-left: 0;
  }

  .Information__ExtraContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-left: 0;
  }

  .Information__ExtraContainer .Information__ExtraContainer-Container:first-child {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 399px) {

  .App {
    background-color: var(--body-background);
    width: 100vw;
    height: -webkit-fill-available;
    padding: 20px 0px;
    margin: 0;
  }

  .Container {
    padding-top: 20px;
    position: relative;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    width: 90%;
    top: 0;
  }

  .Information__Header {
    flex-direction: column;
  }

  .Information__Header-ImageContainer {
    width: 15%;
    border-radius: 100%;
    margin-right: 0;
    margin: auto;
  }

  .Information__Header-InformationContainer {
    flex-direction: column;
    width: 100%;
  }

  .Information__Header-InformationContainer-Left,
  .Information__Header-InformationContainer-Right {
    text-align: center;
  }

  .Search {
    padding: 10px 5px;
  }

  .Search__Input {
    padding: 0px 5px;
  }

  .Search__Button {
    padding: 10px 10px;
  }

  .Information__Header {
    margin-bottom: 15px;
  }

  .Information__Header-InformationContainer {
    flex-direction: column;
  }

  .Information__Header-InformationContainer-Left {
    height: -webkit-min-content;
    height: min-content;
  }

  .Information__Header-InformationContainer-Right {
    margin-left: 0;
  }

  .Information__Header-InformationContainer-Username {
    margin-bottom: 5px;
  }

  .Information__Header-ImageContainer {
    width: 40%;
  }

  .Information__StatsContainer-Container {
    font-size: 12px;
  }

  .Information__BiographyContainer {
    width: 100%;
    margin-left: 0;
    margin-bottom: 25px;
  }

  .Information__StatsContainer {
    width: auto;
    padding: 15px 20px;
    margin-left: 0;
  }

  .Information__ExtraContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-left: 0;
  }

  .Information__ExtraContainer .Information__ExtraContainer-Container:first-child {
    margin-bottom: 10px;
  }
}

.goIn {
  -webkit-animation: goIn .7s ease-in-out forwards;
          animation: goIn .7s ease-in-out forwards;
}

@-webkit-keyframes goIn {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes goIn {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
